export function isValidEmail(target, propertyKey) {
    let value;
    const getter = function () {
        return value;
    };
    const setter = function (newVal) {
        if (!newVal.includes("@")) {
            throw new Error("Niepoprawny adres email");
        }
        value = newVal;
    };
    Object.defineProperty(target, propertyKey, {
        get: getter,
        set: setter,
        enumerable: true,
        configurable: true,
    });
}
export function required(message = "To pole jest wymagane") {
    return (target, propertyKey) => {
        const key = propertyKey.toString();
        let value;
        const getter = function () {
            return value;
        };
        const setter = function (newValue) {
            if (newValue === null || newValue === undefined || String(newValue).trim() === "") {
                throw new Error(message);
            }
            value = newValue;
        };
        Object.defineProperty(target, key, {
            get: getter,
            set: setter,
            enumerable: true,
            configurable: true,
        });
    };
}
export function notEmpty(message = "To pole nie może być puste") {
    return (target, propertyKey) => {
        const key = propertyKey.toString();
        let value;
        const getter = function () {
            return value;
        };
        const setter = function (newValue) {
            if (!newValue || !newValue.trim()) {
                throw new Error(message);
            }
            value = newValue;
        };
        Object.defineProperty(target, key, {
            get: getter,
            set: setter,
            enumerable: true,
            configurable: true,
        });
    };
}
